export const MESSAGES = {
  EMPTY: '  .',
  SOLDOUT: ' .',
  LACK: ' .',
  SUCCESS: '  .'
};

export let totalPrice = 0;

export class VendingMachine {
  constructor(name='', price=0, charge=0) {
    this.name = name;
    this.price = parseInt(price);
    this.charge = parseInt(charge);
  }

  addBasket() {
    this._addPrice();
    return this._addBasketTmpl(this.name, this.price);
  }

  removeBasket(){
    this._distractPrice();
  }

  payCharge(){
    let resultMsg = '';

    if(totalPrice == 0){
      resultMsg = MESSAGES.EMPTY;
    } else if(totalPrice > this.charge){
      resultMsg = `${MESSAGES.LACK}   : ${totalPrice - this.charge}`;
    }else{
      let change = this.charge - totalPrice;
      let changeArr = this._calChange(change);
      resultMsg = `${MESSAGES.SUCCESS}  ${change}  500 ${changeArr[0]}, 100 ${changeArr[1]}, 50 ${changeArr[2]}, 10 ${changeArr[3]} .`;
      this._resetTotalPrice();
    }

    return resultMsg;
  }

  _resetTotalPrice(){
    totalPrice = 0;
  }

  _addPrice(){
    totalPrice = totalPrice + this.price;
  }

  _distractPrice(){
    totalPrice = totalPrice - this.price;
  }

  _calChange(changeParam) {
    let change = changeParam;
    let changeArr = [500, 100, 50, 10];
    let result = [];


    changeArr.forEach(function (coin, index) {
      result.push(Math.floor(change / coin));
      change = change % coin;
    });

    // for(var i = 0; i< changeArr.length; i++){
    //   result.push(Math.floor(change / changeArr[i]));
    //   change = change % changeArr[i];
    // }
    return result;
  }

  _addBasketTmpl(name, price){
    let template = `<li>
                    <span class="inner">
                     ${name} | ${price}
                    </span>
                    <button class="btn_cancel" data-price="${price}">-</button>
                   </li>`;
    return template;
  }

}